export default {
  head() {
    return {
      title: '06-Customer Demo'
    }
  },
  data() {
    return {
      result: [],
    }
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.$axios.$get('/customer')
        .then(res =>
        {
          this.result = res
        }).catch(error=>
      {
        console.log(error)
      })
    },
    viewDetail(id){
      this.$router.push({name:'05-detail', params: {detail:id, flat:true}});
    },
  }
}

